import { Action } from './Action';



//
// Configs type for the different 
//

export type combination_config_t = {
    code: string,
    tolerance?: number,
};

export type keypad_config_t = {
    code: string,
    font?: string,
    layout?: string[][],
}

export type wirecut_config_t = {
    code: string,
    layout: {
        connectorsA: (null|string)[],
        connectorsB: (null|string)[],
        wires: {ini:string|number, end:string|number, color:string}[],
        seed: number,
    }
}

// Global type
export type config_t = combination_config_t | keypad_config_t | wirecut_config_t | undefined;


//
// Puzzle class
//
export class Puzzle {

    type: string;
    config: config_t;
    actions: Action[] = [];
    solved: boolean = false;

    constructor(type: string, config: config_t){
        this.type = type;
        this.config = config;
    }

    add(action: Action){
        this.actions.push(action);
    }

    solve(){
        this.solved = true;
        this.actions.forEach(a => a.run());
    }

}


