
import Game from '../engine/Game';
import Room from '../engine/Room';
import { Puzzle } from '../engine/Puzzle';
import { ActionRoom, ActionMsg } from '../engine/Action';
import Image from '../engine/Image';

export default function createGame(){

    const game = new Game();

    const img1 = new Image(require('../assets/book-open.png'),require('../assets/book-closed.jpg'));
    img1.zoom = true;

    const roomTest = new Room("Habitación de tests", 'open');
    roomTest.add(`
    Estás en al habitación de test. Esta página solo sirve para probar los
    elementos del juego.
    `);
    roomTest.add(`
    Utiliza el menú de lugares para moverte entre localizaciones.
    También hay un libro:
    `);
    roomTest.add(img1);

    roomTest.add(`
    Y unos cuantos cacharros:
    `);


    // 
    roomTest.add(`El primero es un candado con combinación 10-5-15`);
    const puzzle_1 = new Puzzle('combination', {code:'10-5-15'});
    roomTest.add(puzzle_1);

    // 
    roomTest.add(`El segundo es un dispositivo electrónico con extraños símbolos. No sabes la combinación pero pulsar los 4 primos números puede ser una buena opción`);
    const puzzle_2 = new Puzzle('keypad', {
        code:'ABCD',
        layout:[['A','B','C','D','E'],['X','Y','Z','','clear']],
        font:"wizardspeak"

    });
    roomTest.add(puzzle_2);


    //
    roomTest.add(`Por último, hay una panel eléctrico con unos cables sueltos`);
    const puzzle_3 = new Puzzle('wirecut', {
        code:'012',
        layout:{
            connectorsA: [
                'A', 'B', 'C',null,'D'
            ],
            connectorsB: [
                'E', 'F', 'G',
            ],
            wires: [
                {ini:'A', end:1, color:'#4254f5'},
                {ini:1, end:0, color:'#f54242'},
                {ini:'D', end:2, color:'#42f55a'},
            ],
            seed: 2,
        },
    });
    roomTest.add(puzzle_3);







        
   
    game.add(roomTest);

    return game;

}